@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;400&display=swap);
body {
  font-weight: 300;
  font-family: 'Raleway', sans-serif;
}

h1, h2, h3, h4, strong {
  font-weight: 400;
}

h1 {
  margin-bottom: 30px;
}

a, a:hover {
  color: inherit;
  text-decoration: none;
}
.navbar {
  margin-bottom: 40px;
  background-color: #17BEBB;
  
}


.navbar a {
  color: whitesmoke;
}

.toast {
  position: fixed;
  bottom: 10px;
  right: 10px;
  z-index: 99999;
  width: 350px
}

.error-message {
  background: red;
  color: white;
  padding: 10px
}

.btn-primary {
  text-align: center;
  color: black;
  padding: 10px 15px;
  background-color: white;
  border: 1px solid #17BEBB;
  border-radius: 30px;
  transition: all .4s;  
  margin-left: 4px;
}

.btn-primary:hover {
  background-color: #17BEBB;
  border:1px solid #17BEBB;
}
.project-list .card{   
    margin-bottom: 30px;
    
}

.project-list-card{
    border: none;
    margin: 40px 0;
}
.projects-list img {
    height: 160px;
    object-fit: cover;
}

.create-modal {
    margin-top: 10vh;
}
.project-details img {
    width: 100%
}

.project-details ul {
    list-style: none;
}

.project-details ul li {
    margin-bottom: 10px;
}



.back {
    padding: 10px 15px;
    border: 1px solid #17BEBB;
    border-radius: 30px;
    transition: all .4s;
}

.back:hover {
    background-color: #17BEBB;
    color: whitesmoke;
}

.contribution-modal {
    margin-top: 40vh;
}

.contribution-toast {
    margin-bottom: 30vh;
}

.profile-header {
    padding: 10px 0 100px 0;
    /* background-image: url('../../../images/brand_image.svg');
    background-repeat: no-repeat;
    background-size: cover; */
}

.main-container{
    overflow: hidden;
}

.profile-header img {
    width: 120px;
    border-radius: 50%;
    margin-bottom: 20px;;
}

.profile-header article {

    margin: 10px 0 10px 30px;

}

.profile-card {
    border: none;
    margin: 10px -12px;
}

.profile-card p {
    font-size: .8em;
}

.profile-button {
    margin: 30px 0;
}

.projects-profile {
    margin: 0 0 30px 0;
    overflow: scroll;
}
